



export default class RecorderManager {
    recorder = null;
    //录音开始时间
    uniStartTime = null;
    //语音录音中
    recording = false;
    onRecordingComplete = null;

    constructor() {










        this.recorder = uni.getRecorderManager();

    }
    
    onRecordComplete(callBack) {
      this.onRecordingComplete = callBack;

      this.initUniRecorder();

    }

    initUniRecorder() {
        //录音结束后，发送
        this.recorder.onStop((res) => {
            const duration = Date.now() - this.uniStartTime;
            res.duration = duration;
            this.onRecordingComplete(res, duration);
        });
        // 监听录音报错
        this.recorder.onError((e) => {
            this.recording = false;
            this.recorder.stop();
            console.log('录音失败：', e);
        })
    }

    authorize() {
        return new Promise((resolve,reject) => {










            if (uni.authorize) {
                uni.authorize({
                    scope: 'scope.record',
                    success() {
                        resolve()
                    },
                    fail: (e) => {
                        reject(e)
                    }
                });
            }

        });
    }

    start() {
        this.recording = true;






        try {
            // 更多配置参考uniapp：https://uniapp.dcloud.net.cn/api/media/record-manager.html#getrecordermanager
            this.recorder.start({
                duration: 600000 // 指定录音的时长,单位 ms
            });
            this.uniStartTime = Date.now();
        } catch (e) {
            console.log(e);
        }

    }

    stop() {
        this.recording = false;













        try {
            this.recorder.stop();
        } catch (e) {
            console.log(e);
        }

    }

}
